.banner_container {
    display: flex;
    justify-content: center;
    padding: 30px 70px;
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
}

.banner_title {
    font-size: 42px;
    position: absolute;
    font-weight: normal;
    align-self: center;
    color: white;
}

.banner_wrapper {
    display: flex;
    justify-content: space-around;
    border-radius: 20px;
    height: 200px;
    width: 100%;
    filter: brightness(55%);
    background-position: center;
    background-size: cover;
    object-fit: cover;
}

/* Responsive tablet */
@media screen and (max-width: 960px) {
    .banner_title {
        font-size: 30px;
    }
}

/* Responsive mobile */
@media screen and (max-width: 650px) {
    .banner_container {
        padding: 30px 30px;
    }

    .banner_wrapper {
        height: 140px;
    }

    .banner_title {
        font-size: 20px;
        width: 200px;
        text-align: start;
    }
}