.dropdown_container {
    display: flex;
    flex-direction: column;
    margin: 30px 70px;
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
    padding: 0px 70px;
}

/* Responsive mobile */
@media screen and (max-width: 650px) {
    .dropdown_container {
        margin: 30px 30px;
        padding: 0px 0px;
    }
}