.section {
    padding: 0px 70px;
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
}

.section-center {
    margin: 0 auto;
    width: 100%;
    height: 500px;
    max-width: 100%;
    text-align: center;
    position: relative;
    justify-content: center;
    align-items: flex-end;
    display: flex;
    overflow: hidden;
    border-radius: 20px;
}

.prev,
.next {
    position: absolute;
    top: 230px;
    background: transparent;
    color: white;
    display: grid;
    place-items: center;
    border-color: transparent;
    border-radius: 0.25rem;
    cursor: pointer;
    transition: all 0.3s linear;
}

.prev {
    left: 0;
    transform: rotate(-0.50turn);
}

.next {
    right: 0;
}

.prev, .next {
    margin: 0px 25px;
}

.icon-arrow {
    font-size: 40px;
}

.bullet_points {
    z-index: 1;
    color: white;
    font-size: 20px;
    font-weight: bold;
}

.logement_image {
    height: 500px;
    width: 100%;
    object-fit: cover;
    border-radius: 20px;
}

article {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: all 0.3s linear;
}

article.activeSlide {
    opacity: 1;
    transform: translateX(0);
}

article.lastSlide {
    transform: translateX(-100%);
}

article.nextSlide {
    transform: translateX(100%);
}

/* Responsive tablet */
@media screen and (max-width: 900px) {
    .section-center {
        height: 400px;
    }

    .logement_image {
        height: 400px;
    }

    .prev,
    .next {
        top: 175px;
    }
}

/* Responsive mobile */
@media screen and (max-width: 650px) {
    .section {
        padding: 0px 30px;
    }

    .section-center {
        height: 320px;
    }

    .logement_image {
        height: 320px;
    }

    .prev,
    .next {
        top: 142px;
        margin: 0px 6px;
    }

    .icon-arrow {
        font-size: 30px;
    }

    .bullet_points {
        display: none;
    }
}